.button {
  display: block;

  font: inherit;
  font-size: @font-size-link;
  line-height: @line-height-link;

  text-align: center;
  text-transform: uppercase;
  text-decoration: none;

  width: 280px;

  margin: auto;
  padding: 11px 20px 9px;

  border: none;

  background-color: @bg-color-green;
  color: @text-color-white;

  &:hover {
    background-color: #5eaa2f;
  }

  &:active {
    color: #99c684;
  }

  &--promo {
    font-size: 14px;
    line-height: 18px;

    padding-bottom: 11px;
  }

  &--view-all {
    background-color: @bg-color-whitegrey;
    color: @text-color-grey;

    &:hover {
      background-color: #ebebeb;
    }

    &:active {
      color: #c3c3c3;
    }
  }
}

@media (min-width: @tablet-width) {
  .button {
    &--promo {
      font-size: @font-size-link-bigger;

      line-height: @line-height-link;

      width: 245px;

      margin-left: 1px;
      padding: 15px 10px 17px;
    }

    &--card,
    &--view-all {
      width: 250px;
    }

    &--additional {
      width: 634px;
    }

    &--submit {
      flex-shrink: 0;
      font-size: @font-size-link-bigger;

      width: 283px;

      margin: 0;
      padding-top: 15px;
      padding-bottom: 17px;
    }
  }
}

@media (min-width: @desktop-width) {
  .button {
    &--promo {
      margin-left: 0;
    }

    &--card,
    &--view-all {
      width: 170px;
    }

    &--additional {
      width: 245px;

      margin-top: 0;
      margin-right: 0;
      margin-left: 0;
    }

    &--submit {
      width: 568px;
    }
  }
}
